.over {
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); 
    display: flex;
    justify-content: center;
    align-items: center;
    font-size:15px;
}
.script-modal-content {
    background-color: #fff;
    padding: 10px;
    border-radius: 8px;
    position: relative;
    width: 90%;
    max-width: 600px;
    animation: fadeInUp 0.5s ease; 
}
.close {
    position: absolute;
    top: 30px;
    right: 10px;
    cursor: pointer;
    font-size: 30px;
    color: red;
}
.over.closed .script-modal-content {
    animation: fadeOutDown 0.5s ease;
}
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }
    to {
        opacity: 1;
        transform: none;
    }
}
@keyframes fadeOutDown {
    from {
        opacity: 1;
        transform: none;
    }
    to {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }
}
.background7 {
    border-top: 2px solid rgb(100, 29, 29);
    border-bottom: 2px solid rgb(100, 29, 29);
    background: linear-gradient(to right, #366894, #b44248);   padding: 20px; 
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); 
    position: relative; 
    overflow: hidden; 
}.background7::before {
      position: absolute;
      width: 100%;
      height: 100%;
      background-repeat: repeat; 
      opacity: 0.1; 
      z-index: -1; 
}

